import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import logo from '../../assets/logo.png';
import { headerTitle } from '../../constants';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Divider from '@material-ui/core/Divider';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';
import Modal from 'react-bootstrap/Modal';

import { CreateNetworkBox, DeleteNetworkBox } from '../../components';

function Navigation(props) {
    const [menuState, setMenuState] = React.useState(false);
	const [deleteExistingNetworkPopup, setDeleteExistingNetworkPopup] = React.useState(false);
	const [createNewNetworkPopup, setCreateNewNetworkPopup] = React.useState(false);

    const handleDeleteExistingNetworkPopupClose = () => { setDeleteExistingNetworkPopup(false) }
    const handleDeleteExistingNetworkPopupShow = () => { 
        setDeleteExistingNetworkPopup(true);
        setMenuState(false);
    }
    const handleCreateNewNetworkPopupClose = () => { setCreateNewNetworkPopup(false) }
    const handleCreateNewNetworkPopupShow = () => { 
        setCreateNewNetworkPopup(true);
        setMenuState(false);
    }

    const reloadNetworksList = () => {
        props.reloadNetworksList();
    }

    const sideList = () => (
        <div className="Navbar-Side-Menu" role="presentation" >
            <List className="Navbar-Side-Menu-Header">
                <ListItem><img src={logo} className="Navbar-Side-Logo" alt="logo" /></ListItem>
                <ListItem><span className="Navbar-Side-Title">{headerTitle}</span></ListItem>
            </List>
            <Divider />
            <List>
                <ListItem className="Navbar-Menu-List-Item-Header" draggable="false">
                    <ListItemText className="Navbar-Menu-List-Item-Header-Text" primary="Manage networks:" />
                </ListItem>
                <Divider />
                <ListItem className="Navbar-Menu-List-Item-Big" draggable="false" button onClick={handleCreateNewNetworkPopupShow}>
                    <ListItemIcon className="Navbar-Menu-List-Item-Big-Icon"><AddCircleIcon /></ListItemIcon>
                </ListItem>
                <Divider />
                <ListItem className="Navbar-Menu-List-Item-Big" draggable="false" button onClick={handleDeleteExistingNetworkPopupShow}>
                    <ListItemIcon className="Navbar-Menu-List-Item-Big-Icon"><RemoveCircleIcon /></ListItemIcon>
                </ListItem>
                <Divider />
            </List>
        </div>
    )

    return (
        <div>
            <AppBar position="static" className="Navbar-App-Bar">
                <Toolbar className="Navbar-Toolbar">
                    <Typography className="Navbar-Typo">
                        <span className="Navbar-Title">{headerTitle}</span>
                    </Typography>
                    <IconButton disableRipple={true} disableFocusRipple={false} edge="start" className="Navbar-Menu-Icon" color="inherit" aria-label="menu" onClick={() => setMenuState(true)}>
                        <img src={logo} className="Navbar-Logo" alt="logo" />
                        <MenuIcon className="Navbar-Menu-Button" />
                    </IconButton>
                </Toolbar>
            </AppBar>
            <SwipeableDrawer className="Navbar-Side-Menu-Wrapper" anchor="right" open={menuState} onClose={() => setMenuState(false)} onOpen={() => setMenuState(true)}>
                { sideList() }
            </SwipeableDrawer>
            <Modal show={createNewNetworkPopup} onHide={handleCreateNewNetworkPopupClose} centered>
                <CreateNetworkBox close={handleCreateNewNetworkPopupClose} reloadNetworksList={reloadNetworksList} />
            </Modal>
            <Modal show={deleteExistingNetworkPopup} onHide={handleDeleteExistingNetworkPopupClose} centered>
                <DeleteNetworkBox close={handleDeleteExistingNetworkPopupClose} reloadNetworksList={reloadNetworksList} />
            </Modal>
        </div>
    );
};

export default Navigation;